.header {
  font-size: 62.5%;
  margin: 1% auto;
  position: relative;
  height: 80vh;
  background-image: linear-gradient(to right, #ef9a9aad, #64b4f688),
    url("../img/annie-spratt--SG84EOcaJE-unsplash.jpg");
  background-size: cover;
  background-position: center;
  clip-path: polygon(0 0, 100% 0, 100% 91%, 0 100%);
}

.logo-box {
  position: absolute;
  top: 20px;
  left: 20px;
}

.customize {
  color: white;
  font-size: 40px;
}

.heading-primary {
  position: absolute;
  left: 20%;
  top: 30%;
  color: white;
}

.heading-primary-main {
  display: block;
  font-size: 5em;
  left: 30%;
  font-weight: 600;
  letter-spacing: 35px;
  animation: moveInLeft 3s;
  animation-timing-function: ease-out;
}
.heading-primary-sub {
  text-transform: uppercase;
  display: block;
  font-size: 1.5em;
  font-weight: 400;
  letter-spacing: 10px;
  animation: moveInRight 3s;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-20%);
  }
  80% {
    transform: translateX(3%);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(20%);
  }
  80% {
    transform: translateX(-3%);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@media screen and (max-width: 601px) {
  div.header {
    font-size: 60%;
  }
}
